<!---->
<template>
  <div class="admin-check-info-wrapper">

    <h1 class="title"></h1>

    <div class="mode-crumb-box">
      <div class="breadcrumb">
        <i class="iconfont">&#xe61e;</i>
        &nbsp; >&nbsp;
      </div>
      <div class="btn-wrapper">
        <el-button type="warning" plain class="modify-mode-btn"></el-button>
      </div>
    </div>

    <div class="info-wrapper">
        <span class="info-title">
          <i class="iconfont box">&#xe62c;</i>
          
          <el-row :gutter="200" class="info-content" v-for="rowIndex in getRowCount(baseInfo)">
             <el-col :span="baseInfo[getItemIndex(rowIndex, colIndex)].span * 8" class="info-item" v-for="colIndex in 3" v-if="baseInfo[getItemIndex(rowIndex, colIndex)] != null">
              <span class="item-name">{{ baseInfo[getItemIndex(rowIndex, colIndex)].name }}</span>
              <div class="item-content">
              <info-display-temp @clickBtn="btnFunc(baseInfo[getItemIndex(rowIndex, colIndex)])" :item="baseInfo[getItemIndex(rowIndex, colIndex)]"></info-display-temp>
                <!--{{ block.items[getItemIndex(rowIndex, colIndex)].value }}-->
              </div>
            </el-col>
          </el-row>
        </span>
    </div>

    <div class="info-wrapper">
        <span class="info-title">
          <i class="iconfont box">&#xe621;</i>
          
          <el-button type="primary" class="leader-info-check info-detail-check"></el-button>
          <el-row :gutter="200" class="info-content">
             <el-col :span="8" class="info-item">
              <span class="item-name"></span>
              <div class="item-content">
                <el-input disabled v-model="leader.userId"></el-input>
              </div>
            </el-col>
            <el-col :span="8" class="info-item">
              <span class="item-name"></span>
              <div class="item-content">
                <el-input disabled v-model="leader.name"></el-input>
              </div>
            </el-col>
            <el-col :span="8" class="info-item">
              <span class="item-name"></span>
              <div class="item-content">
                 <el-input disabled v-model="leader.userPhone"></el-input>
              </div>
            </el-col>
          </el-row>
        </span>
  </div>


    <div class="info-wrapper">
        <span class="info-title">
          <i class="iconfont box">&#xe64b;</i>
          
          <el-button type="primary" class="teacher-info-check info-detail-check"></el-button>
          <el-row :gutter="200" class="info-content">
             <el-col :span="8" class="info-item">
              <span class="item-name"></span>
              <div class="item-content">
                <el-input disabled v-model="teacher.userId"></el-input>
              </div>
            </el-col>
            <el-col :span="8" class="info-item">
              <span class="item-name"></span>
              <div class="item-content">
                <el-input disabled v-model="teacher.name"></el-input>
              </div>
            </el-col>
            <el-col :span="8" class="info-item">
              <span class="item-name"></span>
              <div class="item-content">
                 <el-input disabled v-model="teacher.userPhone"></el-input>
              </div>
            </el-col>
          </el-row>
        </span>
    </div>


    <div class="info-wrapper">
        <span class="info-title">
          <i class="iconfont box">&#xe62c;</i>
          
          <el-row :gutter="200" class="info-content" v-for="proRow in proInfo">
             <el-col :span="12" class="info-item" v-for="pro in proRow">
              <span class="item-name"></span>
              <div class="item-content">
                <el-input disabled v-model="pro.name"></el-input>
              </div>
               <el-button type="primary" class="teacher-info-check info-detail-check"></el-button>
            </el-col>
          </el-row>
        </span>
    </div>


  </div>
</template>

<script>
  import InfoDisplayTemp from 'components/Admin/InfoOperate/BaseCompent/InfoDisplayTemp'

  const INPUT = 1
  const SELECT = 2
  const RADIO = 3
  const SWITCH = 4
  const BUTTON = 5
  const INPUT_AREA = 6
  const DISPLAY_INFO = [
    {
      key: 'teamName',
      name: '',
      value: '12345',
      type: INPUT,
      span: 1,
      disabled: true
    },
    {
      key: 'teamLeader',
      name: '',
      value: '',
      type: SELECT,
      options: [
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        }
      ],
      span: 1
    },
    {
      key: 'supportOrg',
      name: '',
      value: '',
      type: SELECT,
      options: [
        {
          value: "",
          label: ""
        },
        {
          value: "1",
          label: "1"
        }
      ],
      span: 1
    },
    {
      key: 'teamTeacher',
      name: '',
      value: '',
      type: INPUT,
      span: 1
    },
    {
      key: 'leaderMajor',
      name: '',
      value: '',
      type: INPUT,
      span: 1
    },
    {
      key: 'leaderClass',
      name: '',
      value: '152',
      type: INPUT,
      span: 1
    },
    {
      key: 'teamInfo',
      name: '',
      value: '',
      type: INPUT_AREA,
      span: 3
    },
  ]

  export default {
    data () {
      return {
        baseInfo: DISPLAY_INFO,
        leader: {
          userId: 2015210405043,
          name: "",
          userPhone: 123456789
        },
        teacher: {
          userId: 123456789,
          name: "",
          userPhone: 123456789
        },
        proInfo: [
          [
            {
              name: "pro1"
            },
            {
              name: "pro2"
            }
          ],
          [
            {
              name: "pro3"
            },
            {
              name: "pro4"
            }
          ],
          [
            {
              name: "pro5"
            }
          ],
        ]
      }
    },
    components: {
      InfoDisplayTemp
    },
    methods: {
      getRowCount (arr) {
        return Math.ceil(arr.length / 3)
      },
      getItemIndex (rowIndex, colIndex) {
        return (rowIndex - 1) * 3 + colIndex - 1
      }
    },
  }
</script>

<style scoped>

  .admin-check-info-wrapper {
    /*background-color: #ECF0F1;*/
    background-color: #fff;
    height: 100%;
    padding: 1.4rem 5rem;
  }

  .mode-crumb-box {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }


  h1 {
    display: block;
    text-align: center;
    font-size: 1.7rem;
    padding-bottom: 1.4rem;
    border-bottom: .3rem solid #cbcbcb;

  }

  .breadcrumb {
    padding: 1rem;
    font-size: 1rem;
    font-weight: bold;
    color: #2A3F54;
    display: inline-block;
  }


  .breadcrumb .iconfont {
    font-size: 1.5rem;
    margin-right: .3rem;
  }

  .info-wrapper {
    padding: 2rem 3rem;
  }

  .info-title {
    font-size: 1rem;
    color: #2A3F54;
    font-weight: bold;
  }

  .iconfont.box {
    font-weight: normal;
    margin-right: .3rem;
  }

  .el-row.info-content {
    margin: 1.5rem 0;
  }

  .el-input {
    background-color: #ffff00;
    font-size: .9rem;
  }

  .info-item {
    display: flex;
    align-items: center;
  }

  .item-name {
    font-size: 1rem;
    width: 5rem;
    margin-right: 1rem;
  }

  .item-content {
    flex: 1;
  }

  .info-detail-check {
    height: 1.8rem;
    padding: .3rem;
    margin-left: 1rem;
  }



</style>



