<template>
  <div class="wrap">
    <!---->
    <div class="tagBlock">
      <span v-if="!tagEmpty" style="font-weight: bold; font-size: .9rem;"></span>
      <el-tag v-for="(value,key,index) in filter" v-if="value !== ''" class="tag" >{{keyFormater(key)}}({{valueFormater(key,value,valueLabelMap)}})</el-tag>
    </div>
    <el-button class="addInfo" type="success" size="large" @click="enterAdd"></el-button>
    <el-button class="filter" size="large" @click="enterFilter"></el-button>
    <el-button class="exit-filter" size="large" @click="quitFilter"></el-button>
    <!---->
    <filter-box :dialogVisible="showFilterBox"
                :filter="filter"
                :tmpl = "filterTmpl"
                :valueLabelMap = "valueLabelMap"
                :keyFormatMap = "Object.assign({},keyFormatMap,expandFormatMap)"
                @sendFilter="receiveFilter"></filter-box>
    <info-add :show="showInfoAdd"
              :tmpl = "infoAddTmpl"
              :valueLabelMap = "valueLabelMap"
              :rules = "infoAddRules"
              @sendInfo = "receiveInfo"
    ></info-add>
    <!---->
    <el-table
      :data="tableData"
      stripe
      border
      :row-key="getRowKeys"
      style="width: 100%;">
      <el-table-column type="expand">
        <template scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item v-for="(value, key, index) in expandFormatMap" :label="value">
              <span>{{ props.row[key] }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column
        type="index"
        width="50"
        :resizable="false">
      </el-table-column>
      <el-table-column v-for="(value, key, index) in keyFormatMap"
                       :label="value"
                       :prop = "key"
                       :resizable="false">
      </el-table-column>
      <el-table-column
        label=""
        :resizable="false"
        min-width="150">
        <template scope="scope">
          <el-button
            size="small"
            type="primary"
            @click="handleMore(scope.$index, scope.row)"></el-button>
          <el-button
            size="small"
            class="edit-btn"
            @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button
            size="small"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!---->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-size="pageSize"
        layout="total, prev, pager, next, jumper"
        :total="totalCount">
      </el-pagination>
    </div>
  </div>
</template>
<script>
  import axios from 'axios'
  import ElButton from "../../../../../node_modules/element-ui/packages/button/src/button.vue";
  import FilterBox from "components/Admin/Manage/FilterBox"
  import InfoAdd from "components/Admin/Manage/InfoAdd"
  import * as utils from 'utils/utils'
  export default {
    components: {ElButton, FilterBox, InfoAdd},
    data() {
      return {
        tableData: [{ // 
          id: 1,
          companyName: '1', //
          principalName : '1', //
          companyAccess: '1', //
          status: '1', //
          principalPhone: '1', //
          gender: '1', //
          email: '1', //
          birth: '1', //
          specAddress: '588', //
          fixedTel: '1', //
        }
        ],
        valueLabelMap: { // input
//          role: [{ // 
//            value: 0,
//            label: ''
//          }, {
//            value: 1,
//            label: ''
//          }, {
//            value: 2,
//            label: ''
//          }, {
//            value: 3,
//            label: ''
//          }],
          status: [{
            value: 0,
            label: ''
          }, {
            value: 1,
            label: ''
          }],
          gender: [{
            value: 0,
            label: ''
          }, {
            value: 1,
            label: ''
          }],
        },

        keyFormatMap: { // 
          companyName: '',
          principalName : '',
          companyAccess: '',
          status: '',
        },
        expandFormatMap: { // 
          principalPhone: '',
          gender: '',
          email: '',
          birth: '',
          fixedTel: '',
          specAddress: '',
        },
        infoAddTmpl: {
          companyName: {
            label: '',
            inputType: 0, // 0  input
          },
          principalName: {
            label: '',
            inputType: 0, // 0  input
          },
          companyAccess: {
            label: '',
            inputType: 0,
          },
          status: {
            label: '',
            inputType: 1,
          },
          principalPhone: {
            label: '',
            inputType: 0,
          },
          gender: {
            label: '',
            inputType: 1,
          },
          email: {
            label: '',
            inputType: 0,
          },
          birth: {
            label: '',
            inputType: 2,
          },
          specAddress: {
            label: '',
            inputType: 0,
          },
          fixedTel: {
            label: '',
            inputType: 0,
          },

        },
        infoAddRules: {
          companyName: [
            {required: true, message: '', trigger: 'blur'}
          ],
          principalName: [
            {required: true, message: '', trigger: 'blur'}
          ],
          companyAccess: [
            {required: true, message: '', trigger: 'blur'}
          ],
          status: [
            {required: true, message: '', trigger: 'blur'}
          ],
          principalPhone: [
            {required: true, message: '', trigger: 'blur'}
          ],
          gender: [
            {required: true, message: '', trigger: 'blur'}
          ],
          email: [
            {required: true, message: '', trigger: 'blur'}
          ],
          birth: [
            {required: true, message: '', trigger: 'blur'}
          ],
          specAddress: [
            {required: true, message: '', trigger: 'blur'}
          ],
          fixedTel: [
            {required: true, message: '', trigger: 'blur'}
          ],
        },
//        
        url: '',
        filterTmpl: {
          companyName: {
            label: '',
            inputType: 0, // 0  input
          },
          principalName: {
            label: '',
            inputType: 0, // 0  input
          },
          status: {
            label: '',
            inputType: 1,
          },
          gender: {
            label: '',
            inputType: 1,
          },
          birth: {
            label: '',
            inputType: 2,
          },
          specAddress: {
            label: '',
            inputType: 0,
          }
        },
        filter: {//
          companyName: '', //
          principalName : '', //
          status: '', //
          gender: '', //
          birth: '', //
          specAddress: '', //
        },
        pageSize: 15, //
        currentPage: 1, //
        start: 1, //
        totalCount: 30, //
        showFilterBox: false, // 
        tagEmpty: true, //
        showInfoAdd: false, // 
      }
    },
    mounted: function(){
      this.loadData(this.filter, this.currentName, this.pageSize);
    },
    methods: {
      getRowKeys(row) {
        return row.id;
      },
//        
      loadData(filter, pageNum, pageSize) {
        axios.get(this.url, {param: filter, pageNum: pageNum, pageSize: pageSize})
          .then(function (res) {
            this.tableData = res.data.pagesTableData;
            this.totalCount = res.data.number;
          }, function () {
            console.log('failed')
          }).catch(function (err) {
          console.log(err)
        })
      },
      unique(array) {
        var r = [];
        for(var i = 0, l = array.length; i < l; i++) {
          for(var j = i + 1; j < l; j++)
            if (array[i] === array[j]) j = ++i;
          r.push(array[i]);
        }
        return r;
      },

      handleMore(index, row) {
        this.$router.push('/check/companyInfo/1');
      },
//        
      handleDelete(index, row) {
        var array = [];
        array.push(row.id);
        axios.post('',{"array":array},{emulateJson: true})
          .then(function (res) {
            this.loadData(this.filter, this.currentPage, this.pageSize);
          }, function () {
            console.log('failed');
          })
      },
//        
      handleEdit(index, row) {
        this.$router.push('/edit/companyInfo/1');
      },
//        
      handleSizeChange(val) {
        this.pageSize = val;
        this.loadData(this.filter, this.currentName, this.pageSize);
      },
//        
      handleCurrentChange(val) {
        this.currentPage = val;
        this.loadData(this.filter, this.currentName, this.pageSize);
      },
//        
      enterFilter() {
        this.showFilterBox = true;
      },
//        filterbox
      receiveFilter(filter) {
        if (filter !== undefined)
          this.filter = filter;
        this.showFilterBox = false;
        this.loadData(this.filter, this.currentName, this.pageSize);
      },
//        key
      keyFormater: function(value) {
        if (!value) return '';
        value = value.toString();
        return Object.assign({},this.keyFormatMap,this.expandFormatMap)[value];
      },
      resetObject: utils.resetObject,
      valueFormater: utils.valueFormater,
      quitFilter: function () {
        this.filter = this.resetObject(this.filter);
        this.loadData(this.filter, this.currentName, this.pageSize);
      },
      enterAdd: function () {
        this.showInfoAdd = true;
      },
      receiveInfo: function (data) {
        if (data) {
          axios.post("",{'data':data}, {emulateJson: true})
            .then(function (res) {
              this.loadData(this.filter, this.currentPage, this.pageSize);
            }, function () {
              console.log('failed');
            })
        }
        this.showInfoAdd = false;

      }

    },
    watch: {
      filter: {
        handler: function (val) {
          this.tagEmpty = true;
          for (let item in val) {
            if (val[item] !== '') {
              this.tagEmpty = false;
              break;
            }
          }
        },
        deep: true
      }
    }
  }
</script>
<style scoped>
  .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }

  .wrap {
    position: relative;
    padding: 40px 50px;
  }
  .addInfo {
    float: right;
    margin-right: 40px;
    margin-bottom: 20px;
  }
  .filter {
    float: right;
    margin-right: 20px;
    background-color: #9B59B6;
    color: #ECF0F1;
    outline: 0;
    border: 1px solid #9B59B6;
  }
  .filter:hover {
    opacity: .7;
  }
  .filter:active {
    opacity: 1;
    background-color: #71468B;
  }
  .exit-filter {
    float: right;
    margin-right: 20px;
    background-color: #f19500;
    color: #ECF0F1;
    outline: 0;
    border: 1px solid #f19500;
  }
  .exit-filter:hover {
    opacity: .7;
  }
  .exit-filter:active {
    opacity: 1;
    background-color: #c77800;
  }
  .pagination {
    float: right;
    margin-top: 20px;
  }
  .edit-btn {
    background-color: #5CB85C;
    color: #ECF0F1;
    outline: 0;
    border: 1px solid #5CB85C;
  }
  .edit-btn:hover {
    opacity: .7;
  }
  .edit-btn:active {
    opacity: 1;
    background-color: #4E9B4E;
  }

  .tagBlock {
    display: inline-block;
    margin-top: 10px;
    margin-left: 20%;

  }
  .tag {
    margin: 5px;
  }
</style>




