<template>
  <div>
    <my-header></my-header>

    <div class="container">
      <el-row>
        <el-col :span="24">
          <carousel></carousel>
        </el-col>
      </el-row>

      <el-row>
        <el-col :span="15" class="announcement">
          <router-link to="/"><h2 class="title title-left"></h2></router-link>
          <div class="announcements" ref="indexAnnouncements">
            <announcement v-for="item in announcements" :announcement="item"></announcement>
          </div>
        </el-col>
        <el-col :span="7" :offset="2" class="team-show">
          <h2 class="title title-left"></h2>
          <div class="team-show-wrapper">
            <el-row v-for="item in 3" :gutter="40">
              <el-col :span="12"><team-display></team-display></el-col>
              <el-col :span="12"><team-display></team-display></el-col>
            </el-row>
          </div>
        </el-col>
      </el-row>

      <el-row>
        <el-col :span="24">
          <h2 class="title"></h2>
          <award></award>
          <!--<router-link to="/"><span class="more award-more">&nbsp;&gt; </span></router-link>-->
        </el-col>
      </el-row>

      <el-row :gutter="160">
        <el-col :span="12" class="policy">
          <h2 class="title title-left"></h2>
          <div class="policy-cnt">
            <template v-for="item in 9"><policy-and-file></policy-and-file></template>
          </div>
        </el-col>
        <el-col :span="12" class="school-files">
          <h2 class="title title-left"></h2>
          <div class="files-cnt"><template v-for="item in 9"><policy-and-file></policy-and-file></template></div>
        </el-col>
      </el-row>


    </div>

    <my-footer></my-footer>
  </div>

</template>

<script>
  import Carousel from 'components/Carousel'
  import Announcement from 'components/Announcement'
  import LogAndReg from 'components/LogAndReg'
  import Award from 'components/Award'
  import PolicyAndFile from 'components/PolicyAndFile'
  import TeamDisplay from 'components/TeamDisplay'
  import MyHeader from 'components/MyHeader'
  import MyFooter from 'components/MyFooter'

  const constAnnouncements = [
    {
      date: "2017/10/18",
      items: [
        "",
        "",
        "",
        "",
        "",
      ]
    },
    {
      date: "2017/10/11",
      items: [
        "",
        "",
        ""
      ]
    },
    {
      date: "2017/10/10",
      items: [
        "",
        "",
        ""
      ]
    },
    {
      date: "2017/10/08",
      items: [
        "",
        "",
        ""
      ]
    },
    {
      date: "2017/02/18",
      items: [
        "",
        "",
        ""
      ]
    },
    {
      date: "2017/01/18",
      items: [
        "",
        "",
        ""
      ]
    },
    {
      date: "2016/01/18",
      items: [
        "",
        "",
        ""
      ]
    }
  ]

  export default {
    components: {
      Carousel,
      Announcement,
      LogAndReg,
      Award,
      PolicyAndFile,
      TeamDisplay,
      MyHeader,
      MyFooter
    },
    data () {
      return {
        announcements: []
      }
    },
    mounted () {
        this.announcements = constAnnouncements
        this.$nextTick(this.updateAnnouncements)
    },
    methods: {
      updateAnnouncements () {
        const myBox = this.$refs.indexAnnouncements
        console.log(myBox.clientHeight)
        console.log(myBox.scrollHeight)
        if (myBox.scrollHeight > myBox.clientHeight) {
          let announcement = this.announcements[this.announcements.length - 1]
          if (announcement.items.length > 1) {
            announcement.items.pop()
            console.log(this.announcements[this.announcements.length - 1])
          }
          else {
            this.announcements.pop();
          }
          console.log(announcement)
          this.$nextTick(function () {
            this.updateAnnouncements()
          })
        }
      }
    }
  }
</script>

<style scoped>
  a {
    color: #000;
  }

  .container {
    width: 1140px;
    padding: 0 30px;
    margin: 0 auto;
  }

  .el-row {
    margin: 15px 0;
  }

  .announcement,
  .team-show {
    height: 500px;
    marigin: 10px 0;
    display: flex;
    flex-direction: column;
    overflow: hidden;
  }

  .title {
    padding: 10px 0;
    border-bottom: 3px solid #5c99c8;
  }

  .title-left {
    text-align: left;
  }

  .title-right {
    text-align: right;
  }

  .announcements,
  .team-show-wrapper {
    color: #000;
    padding: 40px 0 0;
    flex: 1;
  }

  .announcements,
  .policy-cnt,
  .files-cnt,
  .team-cnt {
    padding: 40px 35px 0;
  }


  .team-show-wrapper {
    padding: 40px 1.5rem;
  }



  span.more {
    display:  block;
    color: #000;
    font-weight: bold;
    padding-right: 15px;
    text-align: right;
  }

  span.award-more {
    padding-right: 35px;
  }

  .policy,
  .school-files {
  }

  .policy-cnt,
  .files-cnt {
    height: 400px;
    background-origin: content-box;
    background-clip: content-box;
  }

  .team-cnt {
    padding: 40px 35px 0;
    /*background-color: #1d90e6;*/
    background-origin: content-box;
    background-clip: content-box;
  }




</style>



